.timetable{
    .container{
        @extend %container;
    }
    &__title{
        @include text(26px, 400, 32px, $textColor);
        text-align: center;
        margin: 4px 0 41px;
        position: relative;
        @include media(sm) {
            @include text(20px, 500, 29px, $textColor);
            text-align: start;
        }
        &:after{
            content:":";
            position:absolute;
            right: 107px;
            @include media(sm) {
                left:218px;
            }
        }
    }
    &__wrap{
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 60px 220px 220px 100px;
        margin-bottom: 45px;
        @include media(xs) {
            grid-template-rows: 74px 130px 130px 172px;
        }
        @include media(md) {
            grid-template-columns: 707px;
        }
    }
    &__row{
        border: 1px solid $secondaryColor;
        &:first-child{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            @include media(xs) {
                flex-direction: row;
                justify-content: start;
                padding-left: 20px;
                position: relative;
                &::after{
                    content:"";
                    display:block;
                    background-image: url(./../images/calendar-icon.svg);
                    width: 35px;
                    height: 35px;
                    position: absolute;
                    top: 18px;
                    left: 235px;
                }
            }
        }
        &:last-child, &:nth-child(2),&:nth-child(3){
            border-top: none;
        }
        &:nth-child(3){
            border-bottom: none;
        }
        &:last-child{
            @include media(xs) {
                padding-top: 60px;
            }
        }


    }    
    &__meeting-day{
        @include text(18px, 400, 26px, $textColor);
        @include media(xs) {
            text-align: left;
        }
    }    
    
    &__list-wrap{
        @include media(xs){
            display: flex;
            flex-direction: row;
            align-items:center;
            justify-content: space-between;
        } 

    }
    &__list{
        padding: 26px 19px 5px;
    }
    &__item{
        list-style-type: none;
        @include text(18px, 400, 26px, $textColor);
        margin-bottom: 10px;
        &:nth-child(2){
            font-weight: 600;
        }
    }
    &__btn{
        display: block;
        width: 129px;
        height: 50px;
        border: 2px solid $accentColor;
        border-radius: 3px;
        @include text(14px, 500, 17px, $accentColor);
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 0 30px 20px;
        cursor: pointer;
        @include media(xs){
            margin-right: 20px;
        }
    }
    &__icon{
        margin-right: 17px;
    }
    &__form{}
    &__form-item{
        margin: 28px 0 14px 66px;
        position: relative;
        @include media(xs) {
            display: flex;
            // margin: 32px 0 29px 48px;
        }
    }
    &__form-item::before{
        content: url("./../images/rating-cubok.svg");
        position: absolute;
        width: 37px;
        height: 41px;
        top: 1px;
        left: -49px;
        @include media(xs) {
            width: 27px;
            height: 37px;
            top: -20px;
        }
    }
    
    &__form-label{
        position: relative;
        @include media(xs) {
            margin-right: 14px;
        }
    }
    &__link-label{
        @include text(13px, 400, 19px, $textColor);
        display: inline-block;
        // margin: 15px 0 0 10px;
        @include media(xs) {
            @include text(18px, 400, 26px, $textColor);
        }
    }
}